<template>
  <div v-if="block" class="col">
    <div class="card shadow-sm rounded">
      <a :href="block.ytlink"><img :src="require('@/assets/images/'+block.image)" class="card-img-top"></a>
      <div class="card-body">
        <p class="card-text">{{ block.titre }}</p>
          <h4 class="opacity">Ressources</h4>
          <ul>
            <li v-for="(item, index) in block.ressources" :key="index">
            <a class="link-secondary link" @click="onClick(item.lien)">{{ item.titre }}</a>
            </li>
          </ul>
      </div>
    </div>
  </div>
  <div v-else>
    <Loading />
  </div>
</template>

<script>
import Loading from '@/components/Loading.vue'

export default {
  name: 'Block',
  components: {
    Loading,
  },
  props: {
    block: Object,
  },
  methods: {
    onClick(fileName) {
      fetch(location.href+"/ressources/"+fileName, {
        method: 'GET',
        headers: {
            'Content-Type': '\tapplication/text',
            'Authorization': 'Bearer ' + sessionStorage.getItem('token')
        },
      }).then(
        data => {
            return data.blob();
        }
      ).then(
          response => {
            const dataType = response.type;
            const binaryData = [];
            binaryData.push(response);
            var fileLink = document.createElement('a');
            fileLink.href = window.URL.createObjectURL(new Blob(binaryData, { type: dataType }));
            fileLink.setAttribute('download', fileName);
            document.body.appendChild(fileLink);
            fileLink.remove();
          }
      )
    },
  }
}
</script>
