<template>
  <footer class="bg-dark text-secondary px-4 py-5 text-center">
    <div class="py-5">
      <div class="col-lg-6 mx-auto">
        <p v-if="footer" class="fs-5 mb-4">
          <i :class="footer.icon">&nbsp;</i>
          {{ footer.name }} {{ getDateYear }}</p>
        <p v-else>
          <FooterLoading />
        </p>
      </div>
    </div>
  </footer>
</template>

<script>
import FooterLoading from '@/components/FooterLoading.vue'

export default {
  name: 'Footer',
  props: {
    footer: Object,
  },
  components: {
    FooterLoading,
  },
  computed: {
    getDateYear() {
      return new Date().toJSON().slice(0,4).replace(/-/g,'/');
    }
  },
}
</script>

